    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 30%;
    height: 0.2rem;
    background-color: #ccc;
  }
  .text-lineas::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 30%;
    height: 0.2rem;
    background-color: #ccc;
  }
}

