  background-color: #0063c1;
  cursor: pointer;
}
@media (min-width: 768px) {
  .dashboard__boton {
    width: fit-content;
  }
}
.dashboard__contenedor, .dashboard__formulario {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
}
.dashboard__formulario {
  width: min(95%, 80rem);
  margin: 0 auto;
}
.dashboard__grafica {
  width: min(95%, 60rem);
  margin: 0 auto;
}

.table {
  width: 100%;
  border-collapse: collapse;
}
.table__parrafo {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 20rem;
}
.table__thead {
  background-color: #1E293B;
  color: #FFFFFF;
}
.table__th {
  padding: 1rem;
  text-align: left;
}
.table__tr:nth-of-type(even) {
  background-color: #F8FAFC;
}
.table__td, .table__td--acciones {
  padding: 1.5rem 1rem;
}
.table__td--acciones {
  display: flex;
  justify-content: flex-end;
  gap: 2rem;
  align-items: center;
}
.table__td img, .table__td--acciones img {
  width: 15rem;
  object-fit: cover;
}
.table__accion {
  all: unset;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 700;
}
.table__accion--editar {
  color: #007df4;
}
.table__accion--eliminar {
  color: rgb(169, 0, 0);
}
.table__accion:hover {
  cursor: pointer;
}

.paginacion {
  margin-top: 5rem;
  margin-bottom: 3rem;
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .paginacion {
    justify-content: flex-end;
  }
}
.paginacion__enlace {
  color: #007df4;
  font-size: 2rem;
  transition: all 300ms;
}
.paginacion__enlace--texto {
  padding: 1rem;
  border-radius: 0.5rem;
  border: 1px solid #007df4;
}
.paginacion__enlace--texto:hover {
  background-color: #FFFFFF;
  color: #1a1b15;
}
.paginacion__enlace--numero {
  font-weight: 700;
  display: none;
}
@media (min-width: 768px) {
  .paginacion__enlace--numero {
    display: inline;
  }
}
.paginacion__enlace--actual {
  color: #64748B;
  font-weight: 700;
  display: none;
}
@media (min-width: 768px) {
  .paginacion__enlace--actual {
    display: inline;
  }
}

.listado-ponentes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
}
.listado-ponentes__ponente, .listado-ponentes__ponente--seleccionado {
  color: #007df4;
  border: 1px solid #007df4;
  padding: 1rem;
  border-radius: 1rem;
  text-align: center;
}
.listado-ponentes__ponente--seleccionado {
  background-color: #007df4;
  color: #FFFFFF;
}
.listado-ponentes:hover {
  cursor: pointer;
}

.bloques {
  width: min(95%, 140rem);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .bloques {
    margin-top: 10rem;
  }
}
.bloques__grid {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 1024px) {
  .bloques__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.bloque {
  padding: 3rem;
  color: #FFFFFF;
  border-radius: 1rem;
}
.bloque:nth-child(1) {
  background-image: linear-gradient(to left top, #f97316, #fba061);
}
.bloque:nth-child(2) {
  background-image: linear-gradient(to left top, #AB55F7, #cf9efa);
}
.bloque:nth-child(3) {
  background-image: linear-gradient(to left top, rgba(102, 163, 11, 0.4941176471), rgba(147, 235, 16, 0.4941176471));
}
.bloque:nth-child(4) {
  background-image: linear-gradient(to left top, #17769e, #23a7df);
}
.bloque__heading {
  margin: 1rem 0 2rem 0;
  font-size: 3.6rem;
}
.bloque__texto {
  margin: 1rem 0;
}
.bloque__texto--cantidad {
  margin-top: 5rem;
  font-weight: 900;
  font-size: 6rem;
  text-align: center;
}